import Vue from 'vue'
import VueRouter from 'vue-router'
import {routes} from './routes' //routes.js    
import App from './App.vue'
import store from './store'


//use    
Vue.use(VueRouter);


const router = new VueRouter({
  // routes  
  mode: 'history',
  routes
});

new Vue({
  el: '#app',
  router,// 
  store,
  render: h => h(App)
});

axios.defaults.baseURL   = 'http://api.pm0603.com';



