<template>
    <div class="modal" @click="closeModal">
        <div class="modal-background" @click="closeModal"></div>
        <div class="modal-content-md" @click.stop>
                <a role="button" href class="modal-prev-btn" aria-label="prev" @click.prevent="showMainModal">
                    <i class="pe-7s-angle-left" aria-hidden="true"></i>
                </a>
                <div class="modal-header">
                    <h2></h2>
                    <p v-if="!result_fail">{{main_message}}</p>
                    <p v-else class="result-fail">{{alert_message}}</p>
                </div>
                <form enctype="multipart/form-data" method="POST" @submit.prevent="signUp" ref="form">
                <div class="modal-body">
                        <p>
                            <label for="name"></label>
                            <input  type="text" name="username" placeholder=" ." required v-model:name = "name" ref="username">
                        </p>
                        <p>
                            <label for="email"></label>
                            <input  type="text" name="email" placeholder=" ." required v-model:email = "email" ref="email">
                        </p>
                        <p>
                            <label for="password"></label>
                            <input type="password" name="password" placeholder=" ." required v-model:password = "password" ref="password">
                        </p>
                        <p>
                            <label for="password"></label>
                            <input type="password" name="password2" placeholder=" ." v-model = "passwordCheck" class="password2" v-model:passwordtwo="passwordtwo">
                        </p>
                </div>
                <div class="modal-footer">
                    <button type="submit" @click.stop.prevent="signUp" 
                                          @keyup.enter="signUp"></button>
                </div>
                </form>
            </div>
    </div>
</template>
<style lang="scss">
    .modal-header{
        padding-bottom: 4rem;
    }
</style>
<script>
    export default{
        data(){
            return{
                result_fail     : true,
                main_message    : '',
                alert_message   : '',
                name            : '',
                email           : '',
                password        : '',
                passwordtwo     : '',
                passwordCheck   : ''
                
            }
        },
        watch: {
            passwordCheck(new_password){
                this.alert_message = '..';
                this.alert_message = this.password !== new_password ? '  .' : '';
            }
        },
        methods: {
            closeModal(event){
                event.stopPropagation();
                this.$store.commit('setModalStatus',false);
            },
            showMainModal(){
                this.$store.commit('setModalStage', 1);
            },
            // 
            signUp(){
                var _this = this;
                let signData = new FormData(this.$refs.form);

                    axios.post('/user/signup/', signData)
                         .then(function(response){
                                console.log(':',response);

                                let userInfo = {
                                    "name"      :   _this.name, 
                                    "email"     :   _this.email,
                                    "profile"   :   null
                                };

                                _this.$store.commit('setUserInfo',   userInfo);
                                _this.$store.commit('setModalStage', 1 );
                                _this.$store.commit('setModalStatus',false );

                                window.alert(response.data.success + '     .');
                                //  
                                _this.name  = '';
                                _this.email = '';
                                _this.password    = '';
                                _this.passwordtwo = '';

                                // _this.$store.commit('setUserLoginStatus', true );
                                _this.$router.push({ path: '/'});
                                //      ;;
                        })
                        .catch(function(error){
                            if(response.status === 400){
                                 _this.alert_message = '  email .';
                            }else{
                                _this.alert_message  = 'Network Error';
                            }
                            _this.name          = '';
                            _this.password      = '';
                            _this.passwordtwo   = '';
                            
                        });
            }
           
        }
    }

</script>



